    const addDeleteSelectFormFields = (action, childInd, parentInd) => {
        if (action === "add") {
            setItData((prevState) => {
                let tempITData = [...prevState];
                let newEntryData = { ...tempITData[parentInd]?.data?.[childInd], selected_value: '', select_input_type: '' };

                tempITData[parentInd] = {
                    ...tempITData[parentInd],
                    data: [...tempITData[parentInd]?.data, newEntryData]
                };
                return tempITData;
            });
        }
        else if (action === "delete") {
            setItData((prevState) => {
                let tempITData = [...prevState];
                let parentData = { ...tempITData[parentInd] };
                parentData.data = parentData.data.filter((_, ind) => ind !== childInd);
                tempITData[parentInd] = parentData;

                return tempITData;
            });
        }


    };

    const handleChangeData = (e, childInd, parentInd) => {
        const { name, value } = e.target;

        setItData((prevState) =>
            prevState.map((item, index) =>
                index === parentInd
                    ? {
                        ...item,
                        data: item?.data?.map((data, ind) =>
                            ind === childInd ? { ...data, [name]: value } : data
                        ),
                    }
                    : item
            )
        );

    };
    const handleSelectedInputData = (e, childInd, parentInd) => {
        const { name, value } = e.target;
        setItData((prevState) =>
            prevState.map((item, index) =>
                index === parentInd
                    ? {
                        ...item,
                        data: item?.data?.map((data, ind) =>
                            ind === childInd ? { ...data, [name]: value } : data
                        ),
                    }
                    : item
            )
        );
    };

